<template lang="pug">    
    .profile-wrapper
        .form__title Мой профиль
        form.form__profile
          label.label__photo-upload
            input(type='file' @change="handlePhoto").user__form-file-input 
            .user__form-pic(:class="{filled: renderedPhoto.length}" :style="{backgroundImage: `url(${renderedPhoto})`}")
              .user__change-photo Изменить фото
          .user__info
            .form__row
              label.user__info-label Имя
                input(type='text' :value='user.name' size='16' placeholder='Имя').user__name
              label.user__info-label Фамилия
                input(type='text' :value='user.lastname' size='16' placeholder='Фамилия').user__lastname
            .form__row 
              label.user__info-label Пароль
                input(type="password" :value='user.password' size='16' placeholder='Пароль').user__password
              label.user__info-label Повторите пароль
                input(type="password" size='16').user__password
            .form__row  
              label.user__info-label Школа
                input(type='text' :value='user.school' size='16' placeholder='Школа').user__school 
              label.user__info-label Класс
                input(type='number' :value='user.class' placeholder='Класс' min='0' max='12').user__class 
            .form__row
              button(type='submit').submit__button Сохранить
              button(type='reset' @click.prevent='resetButton').reset__button Отмена

</template>
<script>
export default {
  data () {
    return {
      renderedPhoto: '',
      user:{
        name: 'aruzhan',
        lastname: 'kassymova',
        school: '48',
        class: 5,
        password: 'qwerty', 
        photo: ''
      }
    }
  },
  methods: {
    renderImageFile(file) {
      const reader = new FileReader()
      try {
        reader.readAsDataURL(file)
        reader.onloadend=()=>{
          this.renderedPhoto = reader.result
        }
      } catch (err) {
        throw new Error('Error read file')
      } 
    },
    handlePhoto(e) {
      const file = e.target.files[0]
      this.renderImageFile(file)
    },
    resetButton() {
      this.user.name = ''
      this.user.lastname= ''
      this.user.school = ''
      this.user.class = 0
      this.user.password = '' 
      this.renderedPhoto = ''
    }
  }
};
</script>
